import React from "react";
import { useState } from "react";
import Draggable from "react-draggable";
import { useEffect } from "react";
import "./Linktree.css";
import FadeIn from "react-fade-in";


const Linktree = () => {

    let isMobile = window.innerWidth < 1500;
    const [active, setActive] = useState(false);

    const [links,setLinks] = useState([]);

    useEffect(() => {
        // console.log("useeffect");
        fetch("/api")
          .then((response) => response.json())
          .then((data) => setLinks(data.links));
    },[])
    

    //choose the screen size
    console.log(isMobile);
    console.log(window.innerWidth);

      const toggleButton = () => {
        setActive(!active);
      };

    //   const options = {
    //     urls: ["http://linktr.ee/texastalaash"],
    //   };

        // scrape(options).then((result) => {console.log(result)});
      
  return (
    <Draggable
      handle={active ? "" : "button"}
      disabled={window.innerWidth < 700 ? "true" : ""}
    >
      <div className="linktree-container">
        <button className="linktree-button" onClick={toggleButton}>
          <i class={`fa-solid ${active ? "fa-xmark" : "fa-link"}`}></i>
        </button>
        <div className={active ? "linktree-box active" : "linktree-box"}>
          <h1>Useful Links</h1>
          <FadeIn>
            <ul>
              {links.map((link, index) => (
                <li key={index}>
                  <a target="_blank" rel="noreferrer noopener" href={link.url}>
                    {link.title}
                  </a>
                </li>
              ))}
            </ul>
          </FadeIn>
        </div>
      </div>
    </Draggable>
  );
};

export default Linktree;
